import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';

import styled from 'styled-components';

import RenderMarkdown from './RenderMarkdown';

import AppBar from 'material-ui/AppBar';
import Toolbar from 'material-ui/Toolbar';
//import Typography from 'material-ui/Typography';
import RaisedButton from 'material-ui/RaisedButton';
import IconButton from 'material-ui/IconButton';
import MenuIcon from '@material-ui/icons/Menu';

import LeftMenu from './LeftMenu';
import PostsList from './PostsList';
import BooksList from './BooksList';

const styles = {
  root: {
    flexGrow: 1,
  },
  flex: {
    flex: 1,
  },
  menuButton: {
    marginLeft: -12,
    marginRight: 20,
  },
};

const Content = styled.div`
  padding: 20px;
`;

class App extends Component {

  state = {
    url: 'about',
    type: 'page'
  };

  componentDidMount() { }

  getDirByType(type) {
    switch(type) {
      case 'page':
        return 'pages';
      case 'post':
        return 'posts'
    }
  }

  getPage = (url) => {
    const dir = this.getDirByType(this.state.type);

    return require(`./${dir}/${url}.md`);
  }

  handleNavigation = (url, type) => {
    this.setState({ url, type });
  }

  render() {
    return (
      <div className="App">
      <div className="navbar-fixed">
        <nav>
          <div className="nav-wrapper">
            <a href="#" class="brand-logo">Chris Noring</a>
            <ul id="nav-mobile" className="right hide-on-med-and-down">
            </ul>
          </div>
        </nav>
        </div>
        <LeftMenu navigate={this.handleNavigation} />
        <Content>
          {(this.state.type === 'page' || this.state.type === 'post') &&
          <RenderMarkdown url={this.getPage(this.state.url)} />
          }
          { this.state.type === 'posts' &&
           <PostsList navigate={this.handleNavigation} />
          }
          { this.state.type === 'books' &&
            <BooksList />
          }
        </Content>
      </div>
    );
  }
}

export default App;

