import React from 'react';
import Drawer from 'material-ui/Drawer';
import MenuItem from 'material-ui/MenuItem';
import RaisedButton from 'material-ui/RaisedButton';
import styled from 'styled-components';
import AppBar from 'material-ui/AppBar';
import { pages } from './pages/pages';
import './LeftMenu.css';

const AppTitle = styled.div`
  position: fixed;
  z-index: 2;
  height: 60px;
  background: black;
  color: white;
  line-height: 60px;
  text-align: center;
  width: 100%;
  top: 0;

  h1{
    margin: 0;
  }
`;

const DrawerWrapper = styled(Drawer)`
  margin-top: 60px;
`;

let style = {
  width: '0'
}

const Hamburger = styled.div`
  position: fixed;
  top: 9px;
  z-index: 1001;
  color: white;
  left: 20px;


  @media screen and (max-width: 450px) {
    top: 4px;
  }
`;

const Item = styled.div`
  font-size: 20px;
`

export default class LeftMenu extends React.Component {

  state = {
    open : false,
    menu: pages
  };

  constructor(props) {
    super(props);
  }

  componentDidMount() {
  }

  menuClick = (url, type) => {
    this.props.navigate(url, type);
    this.setState({ open: false });
  }

  toggleNav = () => {
    //const elem = document.getElementById('mySidenav');
    this.setState({ open: !this.state.open });
    // elem.style.width = "250px";
  }

  closeNav = () => {
    //const elem = document.getElementById('mySidenav');
    //elem.style.width = "0";
    this.setState({ open: false });
  }

  render() {

    return (
      <React.Fragment>
        <Hamburger>
          <span className="open" onClick={this.toggleNav} >&#9776;</span>
        </Hamburger>
        <div id="sidenav" className={ this.state.open ? 'open': '' }>
          <a href="javascript:void(0)" className="closebtn" onClick={this.closeNav} >&times;</a>
          {this.state.menu.map( item => <Item onClick={() => this.menuClick(item.url, item.type)}>{item.title}</Item>)}
        </div>
      </React.Fragment>
    );
  }
}






