import React from 'react';
import styled from 'styled-components';
import gitbook from './gitbook.png';

const BookImage = styled.img`
  width: 100%;
  max-width: 300px;
  max-height: 350px;

  @media screen and (min-width: 600px) {
    width: inherit;
  }
`;

const BookWrapper = styled.div`
  margin: 10px;
  box-shadow: 0 0 5px gray;
  padding: 20px;
  width: 400px;
  display: flex;
  flex-direction: column;
  text-align: center;
  min-height: 460px;

  @media screen and (max-width: 600px) {
    width: inherit;
  }

`;

const BooksWrapper = styled.div`
  display: flex;
  flex-direction: row;
  @media screen and (max-width: 600px) {
    flex-direction: column;
  }

  flex-wrap: wrap;
`;

const BookLink = styled.div`
  flex-grow: 1;
`

const books = [{
  imageUrl: 'https://images-na.ssl-images-amazon.com/images/I/5170uEMyl8L.jpg',
  title: 'Learning Angular',
  externalUrl: 'https://www.amazon.com/Learning-Angular-Second-Christoffer-Noring-ebook/dp/B075DG97F7/ref=la_B0778XCXSL_1_1?s=books&ie=UTF8&qid=1512694217&sr=1-1'
},
{
  imageUrl: 'https://images-na.ssl-images-amazon.com/images/I/415Uhm43mAL._SX404_BO1,204,203,200_.jpg',
  title: 'Architecting Angular Applications with Redux, RxJS, and NgRx',
  externalUrl: 'https://www.amazon.com/Architecting-Angular-Applications-Flux-Redux/dp/1787122409/ref=sr_1_1?s=books&ie=UTF8&qid=1523617160&sr=1-1&keywords=ngrx'
}, {
  title: 'RxJS Ultimate',
  externalUrl: 'https://legacy.gitbook.com/book/chrisnoring/rxjs-5-ultimate/details',
  imageUrl: gitbook
},
{
  title: 'React',
  externalUrl: 'https://legacy.gitbook.com/book/chrisnoring/react/details',
  imageUrl: gitbook
}]

const Book = ({ book: { title, imageUrl, externalUrl } }) => (
  <BookWrapper>
    <div>
      <h4>{title}</h4>
    </div>
    <BookLink>
      <a target="_" href={externalUrl}>External link to Book</a>
    </BookLink>
    <div>
      <BookImage src={imageUrl} title="image of book" />
    </div>
  </BookWrapper>
);

const BooksList = () => (
  <BooksWrapper>
    {books.map(book => <Book book={book} />)}
  </BooksWrapper>
);

export default BooksList;



