import React, { Component } from 'react';
import PropTypes from 'prop-types';

import marked from "marked";

class RenderMarkdown extends React.Component {
  static propTypes = {
    url: PropTypes.string.isRequired,
  };

  state = {
    data: void 0,
    loading: false,
    error: void 0
  };

  componentDidUpdate() {
    var els = document.querySelectorAll('pre code');
    for (var i = 0; i < els.length; i++) {
      if (!els[i].classList.contains('hljs')) {
        window.hljs.highlightBlock(els[i]);
      }
    }
  }

  componentDidMount() {
    this.getSource(this.props.url);
  }

  componentWillReceiveProps(nextProps) {
    if(this.props.url !== nextProps.url) {
      this.getSource(nextProps.url);
    }
  }

  getSource(url) {
    this.setState({ loading: true });

    fetch(url)
    .then(response => response.text())
    .then(data => {
      this.setState({ data, loading: false });
    })
    .catch(err => {
      this.setState({ error: err, loading: false });
    })
  }

  render() {
    const { data, error, loading } = this.state;
    if(data) {
      return (
        <p className="content"
           dangerouslySetInnerHTML={{__html: marked(data)}}
        />);
    } else if(error) {
      return (
        <div>{error}</div>
      )
    } else if(loading) {
      return (
        <div>loading...</div>
      )
    }
    return null;

  }
}

export default RenderMarkdown;



